import styled from 'styled-components'
import bg_img from '../utils/wallpaper.jpg'
import globalValues from '../utils/globalValues'
import { Link } from 'react-router-dom'
import Footer from './Footer'

export default function Home() {
  return (
    <Wrapper>
      <section>
        <div>
          <h4>best online store of the year</h4>
          <h1>Where something special happens every day.</h1>
          <img
            src='data:image/png;base64,'
            alt='underline'
          ></img>
          <Link to='/shopping-cart/products'>
            <button>Shop Now</button>
          </Link>
        </div>
      </section>
      <Footer color='white' />
    </Wrapper>
  )
}

const Wrapper = styled.main`
  height: ${globalValues.main_body_height};
  box-shadow: inset 0 0 8rem #000000;
  position: relative;
  min-height: 30rem;
  section {
    margin-inline: auto;
    max-width: ${globalValues.navbar_content_max_width};
    padding: 2rem 0 0 2rem;
    font-weight: 800;
    @media (max-width: ${globalValues.media_query_mobile_length}) {
      padding-left: 0;
    }
    div {
      width: 26rem;
      @media (max-width: ${globalValues.media_query_mobile_length}) {
        text-align: center;
        margin-inline: auto;
      }
      h4 {
        color: #ffffffac;
        text-transform: uppercase;
        margin-bottom: 0.5rem;
      }
      h1 {
        color: white;
        font-size: 2.5rem;
        line-height: 1.5;
        margin-bottom: 1rem;
      }
      img {
        width: 10rem;
        margin-top: 1rem;
        margin-bottom: 2rem;
        @media (max-width: ${globalValues.media_query_mobile_length}) {
          display: none;
        }
      }
      button {
        font-family: inherit;
        color: white;
        font-size: 2rem;
        font-weight: 700;
        padding: 1rem 4.5rem;
        border-radius: 50px;
        border: none;
        background-color: black;
        ${globalValues.button_hover_effect}
      }
    }
  }

  /* background image stylings */
  background-image: url(${bg_img});
  background-size: cover;
  background-repeat: no-repeat;
  background-position: right;
  @media (max-width: ${globalValues.media_query_mobile_length}) {
    background-position: 87% 0%;
    height: calc(100vh - 9.8rem);
  }

  ${globalValues.page_switch}
`
